// color

//gray
$white: #fff;
$gray-200:#DCDCDC; /* lightGray */
$gray-300: #707070; /* gray */
$gray-400: #6C6C6C;
$gray-500:#555555;
$gray-600:#212529; /* grayBlack */
$gray-700: #111619; /* mainBlack */
$gray-800: #0A0A0A; /* gray 700 */
$black: #000;

//gray map
$grays: (
  //"100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
  "700": $gray-700,
  "800": $gray-800,
  //"900": $gray-900
  "white": $white,
  "black": $black
);

//red oranges
$red-100: #fbf2f2;
$red-400: #aa0601;
$red-800: #650300;
$org-300: #b75929;
$org-800: #5F3E2D;

//bootstrap theme colors
$primary:$red-400;
$secondary:$red-100;
$danger: $red-800;
$warning: $org-300;
$info:$org-800;
//$success

//theme map
$theme-colors: (
  "primary":    $primary,
  "secondary":  $secondary,
  //"success":    $success,
  "info":       $info,
  "warning":    $warning,
  "danger":     $danger,
  "white":      $white,
  "black":       $black
);


$font-size-base: 1rem;
//font-size
$font-sizes: (
  "1": $font-size-base *4,
  "2": $font-size-base * 2.5, // 40 
  "3": $font-size-base * 3, // 48
  "4": $font-size-base * 2.25,  //36
  "5": $font-size-base * 2,   // 32
  "6": $font-size-base * 1.5, //24 
  "7": $font-size-base * 1.25,  // 20
  "8": $font-size-base * 1.125, // 18 
  "9": $font-size-base, 
  "10": $font-size-base * .875 //14
);


// spacer-setting reference from bs5 
// 方向
$directions: (
    "+": "" ,       // '+' means all directions
    "x": "",        // 'x' means x axis
    "y": "",        // 'y' means y axis
    "t": "top",
    "r": "right",
    "b": "bottom",
    "l": "left"
);

$spacer: 1rem; //16px

$spacers: (
  "0": 0,
  "1": $spacer / 4,  //4
  "2": $spacer / 2,  //8
  "3": $spacer * .875, //14
  "4": $spacer ,        //16
  "5": $spacer * 1.25,  // 20
  "6": $spacer * 1.5, //24 
  "7": $spacer * 1.75, // 28
  "8": $spacer * 1.875, // 30
  "9": $spacer * 2,   // 32
  "10": $spacer * 2.25,  //36
  "11": $spacer * 2.5, // 40 
  "12": $spacer * 3, // 48
  "13": $spacer * 3.5, //56
  "14": $spacer * 3.75, //60 
  "15": $spacer * 4,  // 64
  "16": $spacer * 4.5, // 72 
  "17": $spacer * 6, //96
  "18": $spacer * 10  //160
);


$spacers-md: (
  "0": 0,
  "1": $spacer / 4,  //4
  "2": $spacer / 2,  //8
  "3": $spacer * .875, //14
  "4": $spacer ,        //16
  "5": $spacer * 1.25,  // 20
  "6": $spacer * 1.5, //24 
  "7": $spacer * 1.75, // 28
  "8": $spacer * 1.875, // 30
  "9": $spacer * 2,   // 32
  "10": $spacer * 2.25,  //36
  "11": $spacer * 2.5, // 40 
  "12": $spacer * 3 // 48
);

$spacers-sm: (
  "0": 0,
  "1": $spacer / 4,  //4
  "2": $spacer / 2,  //8
  "3": $spacer * .875, //14
  "4": $spacer ,        //16
  "5": $spacer * 1.25,  // 20
  "6": $spacer * 1.5, //24 
  "7": $spacer * 1.75, // 28
  "8": $spacer * 1.875, // 30
  "9": $spacer * 2,   // 32
  "10": $spacer * 2.25,  //36
  "11": $spacer * 2.5, // 40 
  "12": $spacer * 3 // 48
);