//util-colors
//background-color
@each $key, $value in $theme-colors{
    .bg-#{$key}{
      background-color: $value;
    }
  }
  
  //text-color
  @each $key, $value in $theme-colors{
    .text-#{$key}{
      color: $value;
    }
  }
  @each $key, $value in $grays {
    .text-#{$key}{
      color: $value;
    }
    
  }
  
  //border-color
  @each $key, $value in $grays{
    .bd-#{$key}{
      border: 1px solid $value;
    }
  }